import { useState } from 'react'
import ReactGA from 'react-ga'
import NumberFormat from 'react-number-format'
import * as yup from 'yup'

const errorText = {
  priceError: () => 'سعر الوحدة لا يقل عن 100,000 ولا يزيد عن 2,500,000',
  yearsError: () => 'عدد السنوات لا يقل عن 3 ولا يزيد عن 30',
  percentError: () => 'نسبة المقدم لا تقل عن 15% ولا تزيد عن 75%',
  typeError: () => 'ادخل رقمًا صحيحًا',
}

const Calculator = props => {
  const { state, setState, setResults, viewHandler } = props

  const [errors, setErrors] = useState({})

  const schema = yup.object().shape({
    price: yup.number()
      .typeError(errorText.typeError())
      .required('يجب إدخال السعر')
      .positive()
      .min(100000, errorText.priceError())
      .max(2500000, errorText.priceError()),
    years: yup.number()
      .typeError(errorText.typeError())
      .required('يجب إدخال عدد السنوات')
      .positive()
      .integer()
      .min(3, errorText.yearsError())
      .max(30, errorText.yearsError()),
    advPercent: yup.number()
      .typeError(errorText.typeError())
      .required('يجب إدخال نسبة المقدم')
      .positive()
      .min(15, errorText.percentError())
      .max(75, errorText.percentError()),
  });
  
  const handlePriceChange = price => {
    setState(state => ({ ...state, price: price }))
    setState(state => ({ ...state, advance: price * (state.advPercent / 100) }))
  }

  const handleInitPercent = percent => {
    const total = parseFloat(state.price)
    setState(state => ({ ...state, advPercent: percent }))
    setState(state => ({ ...state, advance: total * (percent / 100) }))
  }

  const clear = e => {
    e.preventDefault()
    const confirmed = window.confirm('تصفير الحقول؟')
    if (confirmed) {
      setErrors({})
      setState({
        price: 0,
        years: 0,
        interest: 'i3',
        rentType: 'fixed',
        serviceType: 'in',
        advance: 0,
        advPercent: '',
      })
    }
  }
  
  const submit = e => {
    e.preventDefault()
    ReactGA.event({
      category: 'Button',
      action: 'Calculate',
    })
    schema
      .validate({
          price: state.price,
          years: state.years,
          advPercent: state.advPercent
        },
        { abortEarly: false }
      )
      .then(() => {
        setErrors({})
        estimate()
      })
      .catch(errors => {
        const errorList = {}
        const errorsArray = errors.inner || []
        errorsArray.forEach(err => errorList[err.path] = err.message)
        setErrors(errorList)
      })
  }

  const rentSearch = rent => {
    let remainingYears = parseInt(state.years, 10)
    let interestPercent = state.interest === 'i3' ? 0.03 : 0.08
    let remainingPrice = state.serviceType === 'in'
      ? (state.price + state.price * 0.05 - state.advance)
      : (state.price - state.advance)
    let rentValues = []
    while (remainingYears) {
      remainingPrice += (remainingPrice * interestPercent)
      remainingPrice -= (rent * 12)
      remainingYears -= 1
      if (state.rentType === 'inc') {
        rentValues.push(rent)
        rent += rent * 0.07
      }
    }
    return [remainingPrice, rentValues]
  }

  const estimate = () => {
    let low = 1
    let high = 50000
    let rent = (low + high) / 2
    let [remainingPrice, rentValues] = rentSearch(rent)
    while (Math.abs(remainingPrice) > 1) {
      if (remainingPrice > 0) {
        low = rent + 0.1
      } else {
        high = rent - 0.1
      }
      rent = (low + high) / 2;
      [remainingPrice, rentValues] = rentSearch(rent)
    }
    let loan = state.serviceType === 'in'
      ? (state.price + state.price * 0.05 - state.advance)
      : (state.price - state.advance)
    let rentSum = state.rentType === 'inc'
      ? (rentValues.reduce((total, p) => total += p * 12, 0))
      : (rent * 12 * state.years)
    // console.log(rent, rentValues)
    setResults({
      rent: state.rentType === 'inc' ? rentValues : rent,
      totalUnit: state.price,
      totalCash: state.advance + (state.serviceType === 'in' ? 0 : state.price * 0.05),
      totalLoan: loan,
      totalInterest: (rentSum - loan).toFixed(0),
      totalPaid: (rentSum + state.advance).toFixed(0),
      minSalary: (state.rentType === 'inc' ? rentValues[0] : rent) * 2.5,
    })
    viewHandler(1)
  }

  return (
    <form className="form-container">
      {/* Price */}
      <div className="form-group w-100">
        <label className="bold" htmlFor="price"> سعر الوحدة </label>
        <NumberFormat
          name="price"
          value={state.price}
          thousandSeparator={true}
          onValueChange={num => handlePriceChange(num.floatValue)}
        />
        {Boolean(errors.price !== null) && <p className='error-message'> {errors.price} </p>}
      </div>
      {/* Advance  */}
      <div className="form-group">
        <label className="bold" htmlFor="initPercent"> نسبة المقدم </label>
        <NumberFormat
          name="initPercent"
          value={state.advPercent}
          thousandSeparator={true}
          prefix="%"
          onValueChange={num => handleInitPercent(num.floatValue)}
        />
        {Boolean(errors.advPercent !== null) && <p className='error-message'> {errors.advPercent} </p>}
      </div>
      {/* Years  */}
      <div className="form-group">
        <label className="bold" htmlFor="years"> سنوات التقسيط </label>
        <input
          name="years"
          type="number"
          step="1"
          value={state.years}
          onChange={e => setState(state => ({ ...state, years: e.target.value }))}
        />
        {Boolean(errors.years !== null) && <p className='error-message'> {errors.years} </p>}
      </div>
      <div className="flex w-100">
        {/* Interest */}
        <div className="form-group w-30">
          <label className="bold" htmlFor="interest"> نظام الفائدة </label>
          <div className="radio-group flex">
            <div>
              <input
                name="interest"
                type="radio"
                id="i3"
                value="i3"
                checked={state.interest === 'i3'}
                onChange={e => setState(state => ({ ...state, interest: e.target.value }))}
              />
              <label htmlFor="i3"> 3% متناقصة </label>
            </div>
            <div>
              <input
                name="interest"
                type="radio"
                id="i8"
                value="i8"
                checked={state.interest === 'i8'}
                onChange={e => setState(state => ({ ...state, interest: e.target.value }))}
              />
              <label htmlFor="i8"> 8% متناقصة </label>
            </div>
          </div>
        </div>
        {/* Rent Type */}
        <div className="form-group w-30">
          <label className="bold" htmlFor="rentType"> نظام القسط </label>
          <div className="radio-group flex">
            <div>
              <input
                name="rentType"
                type="radio"
                id="fixed"
                value="fixed"
                checked={state.rentType === 'fixed'}
                onChange={e => setState(state => ({ ...state, rentType: e.target.value }))}
              />
              <label htmlFor="fixed"> ثابت </label>
            </div>
            <div>
              <input
                name="rentType"
                type="radio"
                id="inc"
                value="inc"
                checked={state.rentType === 'inc'}
                onChange={e => setState(state => ({ ...state, rentType: e.target.value }))}
              />
              <label htmlFor="inc"> متزايد 7% </label>
            </div>
          </div>
        </div>
        {/* Service Type */}
        <div className="form-group w-30">
          <label className="bold" htmlFor="serviceType"> نظام الوديعة </label>
          <div className="radio-group flex">
            <div>
              <input
                name="serviceType"
                type="radio"
                id="in"
                value="in"
                checked={state.serviceType === 'in'}
                onChange={e => setState(state => ({ ...state, serviceType: e.target.value }))}
              />
              <label htmlFor="in"> داخل القرض </label>
            </div>
            <div>
              <input
                name="serviceType"
                type="radio"
                id="out"
                value="out"
                checked={state.serviceType === 'out'}
                onChange={e => setState(state => ({ ...state, serviceType: e.target.value }))}
              />
              <label htmlFor="out"> خارج القرض </label>
            </div>
          </div>
        </div>
      </div>
      {/* Advance Value  */}
      <div className="form-group w-30">
        <label className="bold" htmlFor="init"> مبلغ المقدم </label>
        <NumberFormat
          name="init"
          value={state.advance.toFixed(2)}
          thousandSeparator={true}
          disabled
        />
      </div>
      {/* Loan */}
      <div className="form-group w-30">
        <label className="bold" htmlFor="init"> القرض </label>
        <NumberFormat
          name="init"
          value={state.serviceType === 'in' ? (state.price + state.price * 0.05 - state.advance) : (state.price - state.advance)}
          thousandSeparator={true}
          disabled
        />
      </div>
      {/* Service 5%  */}
      <div className="form-group w-30">
        <label className="bold" htmlFor="init"> وديعة الصيانة 5% </label>
        <NumberFormat
          name="init"
          value={(state.price * 0.05).toFixed(2)}
          thousandSeparator={true}
          disabled
        />
      </div>
      <div className="form-group w-100">
        يضاف 1.5% رسوم إدارية: ({state.price * 0.015 || 0})
      </div>
      <div className="form-group text-center w-100">
        <input
          type="submit"
          value="إحسب"
          onClick={e => submit(e)}
        />
        <input
          type="reset"
          value="مسح"
          onClick={e => clear(e)}
        />
      </div>
    </form>
  )
}

export default Calculator