import NumberFormat from 'react-number-format'

const Result = props => {
  const { results, viewHandler, serviceCash } = props
  return (
    <div>
      {
        Boolean(Object.keys(results).length)
        && <div className="result">
            <table>
              <tbody style={{ verticalAlign: 'baseline' }}>
                <tr>
                  <td> سعر الوحدة </td>
                  <td>
                    <NumberFormat
                      value={parseInt(results.totalUnit, 10)}
                      thousandSeparator={true}
                      displayType="text"
                    />
                  </td>
                </tr>
                <tr>
                  <td>
                    المدفوع كاش
                    <small className="help">
                      ({`المقدم + رسوم إدارية 1.5%${serviceCash ? ' + وديعة الصيانة' : ''}`})
                    </small>
                  </td>
                  <td>
                    <NumberFormat
                      value={parseInt(results.totalCash, 10) + parseInt(results.totalUnit * 0.015, 10)}
                      thousandSeparator={true}
                      displayType="text"
                    />
                  </td>
                </tr>
                <tr>
                  <td>
                    اجمالي القرض
                    <small className="help">
                      ({`${serviceCash ? 'غير شامل وديعة الصيانة' : 'شامل وديعة الصيانة'}`})
                    </small>
                  </td>
                  <td>
                    <NumberFormat
                      value={parseInt(results.totalLoan, 10)}
                      thousandSeparator={true}
                      displayType="text"
                    />
                  </td>
                </tr>
                {Array.isArray(results.rent)
                  ? <>
                      <tr>
                        <td style={{ color: 'blue' }}> الأقساط الشهرية </td>
                        <td>
                          {results.rent.map((rent, index) =>
                            <p key={index} className="m-0">
                              السنة ({index + 1}):
                              <NumberFormat
                                className="mr-1"
                                value={parseInt(rent, 10)}
                                thousandSeparator={true}
                                displayType="text"
                              />
                            </p>
                          )}
                        </td>
                      </tr>
                      <tr>
                        <td> متوسط القسط الشهري </td>
                        <td>
                          <NumberFormat
                            value={parseInt(results.rent.reduce((total, p) => total += p, 0) / results.rent.length, 10)}
                            thousandSeparator={true}
                            displayType="text"
                          />
                        </td>
                      </tr>
                    </>
                  : <tr style={{ color: 'blue' }}>
                      <td> القسط الشهري </td>
                      <td>
                        <NumberFormat
                          value={parseInt(results.rent, 10)}
                          thousandSeparator={true}
                          displayType="text"
                        />
                      </td>
                    </tr>
                  }
                <tr>
                  <td> الحد الأدني للدخل عند التعاقد </td>
                  <td>
                    <NumberFormat
                      value={parseInt(results.minSalary, 10)}
                      thousandSeparator={true}
                      displayType="text"
                    />
                  </td>
                </tr>
                <tr>
                  <td> اجمالي الفائدة </td>
                  <td>
                    <NumberFormat
                      value={parseInt(results.totalInterest, 10)}
                      thousandSeparator={true}
                      displayType="text"
                    />
                  </td>
                </tr>
                <tr>
                  <td> اجمالي المدفوع مع نهاية المدة </td>
                  <td>
                    <NumberFormat
                      value={parseInt(results.totalPaid, 10)}
                      thousandSeparator={true}
                      displayType="text"
                    />
                  </td>
                </tr>
              </tbody>
            </table>
            <small> النتيجة تقريبية فقط</small>
          </div>
      }
      <div className="text-center">
        <input onClick={e => { e.preventDefault(); viewHandler(0); }} type="reset" value="العودة" />
      </div>
    </div>
  )
}

export default Result