import { useEffect, useState } from 'react'
import ReactGA from 'react-ga'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { library } from '@fortawesome/fontawesome-svg-core'
import { faFacebookSquare, faYoutube, faLinkedin, faGithubSquare } from '@fortawesome/free-brands-svg-icons'
import Calculator from './views/Calculator'
import Result from './views/Result'
import './assets/css/app.css'

library.add(faFacebookSquare, faYoutube, faLinkedin, faGithubSquare)

const App = () => {
  const [view, setView] = useState(0)
  const [state, setState] = useState({
    price: 0,
    years: 0,
    interest: 'i3',
    rentType: 'fixed',
    serviceType: 'in',
    advance: 0,
    advPercent: '',
  })
  const [results, setResults] = useState({
    rent: 0,
    totalUnit: 0,
    totalCash: 0,
    totalLoan: 0,
    totalInterest: 0,
    totalPaid: 0,
    minSalary: 0,
  })

  useEffect(() => {
    ReactGA.initialize('G-11HKFLJ56F')
  }, [])

  return (
    <div className="App" dir="rtl">
      <div className="bg-image" />
      <div className="layout">
        <div className="calc-container">
          <h2>حساب أقساط الوحدة</h2>
          <hr />
            {view === 0
              ? <Calculator
                  state={state}
                  setState={setState}
                  setResults={setResults}
                  viewHandler={setView}
                />
              : <Result
                  results={results}
                  viewHandler={setView}
                  serviceCash={state.serviceType === 'out'}
                />}
        </div>
      </div>
      <div className="footer">
        <div className="about-text text-center">
          <small>
            هذه الحاسبة خاصة بحساب اقساط وحدات وزارة الإسكان المطروحة بنظام التمويل العقاري، وهي غير رسمية وتعتبر النتائج غير دقيقة ولكن قريبة بشكل كبير للمساعدة في إيضاح نظام السداد.
          </small>
        </div>
        <p>
          تطوير
          <a href={window.atob('aHR0cHM6Ly93d3cubGlua2VkaW4uY29tL2luL2FiZGVscmFobWFuYWJkZWxoYW1pZC8=')} target="_blank" rel="noreferrer" className="mr-1">
            م. عبدالرحمن طارق
          </a>
        </p>
        <div className="icons">
          <a className="youtube" href={window.atob('aHR0cHM6Ly93d3cueW91dHViZS5jb20vYy9EZXZUYXJlaw==')} target="_blank" rel="noreferrer">
            <FontAwesomeIcon icon={faYoutube} />
          </a>
          <a className="facebook" href={window.atob('aHR0cHM6Ly9mYi5jb20vZGV2dGFyZWs=')} target="_blank" rel="noreferrer">
            <FontAwesomeIcon icon={faFacebookSquare} />
          </a>
          <a className="github" href={window.atob('aHR0cHM6Ly9naXRodWIuY29tL2Rldi10YXJlaw==')} target="_blank" rel="noreferrer">
            <FontAwesomeIcon icon={faGithubSquare} />
          </a>
          <a className="linkedin" href={window.atob('aHR0cHM6Ly93d3cubGlua2VkaW4uY29tL2luL2FiZGVscmFobWFuYWJkZWxoYW1pZC8=')}target="_blank" rel="noreferrer">
            <FontAwesomeIcon icon={faLinkedin} />
          </a>
        </div>
      </div>
    </div>
  )
}

export default App
